import * as React from "react";
import * as echarts from "echarts";

export default class extends React.Component<{
    xData: any[],
    min: number,
    max: number
}, {}> {
    private containerRef: any;
    private chartRef: any;
    private chart: any;

    constructor(props: any) {
        super(props);
        this.containerRef = React.createRef();
        this.chartRef = React.createRef();
    }

    public componentDidMount() {
        this.chart = echarts.init(this.chartRef.current);
        this.draw()
    }

    public componentDidUpdate() {
        this.draw()
    }

    public draw() {
        let { xData, min, max } = this.props;
        const option = {

            series: [
                {
                    name: '',
                    type: 'gauge',
                    min: min || 0,
                    max: max || 100,
                    detail: {
                        formatter: '{value}',
                        offsetCenter: [0, '95%'],
                        fontSize: 17,
                        color: 'rgba(195, 229, 249, 1)'
                    },
                    data: xData,
                    title: {  //
                        offsetCenter: [0, '70%'],
                        fontSize: 12,
                        color: 'rgba(195, 229, 249, 0.7)'
                    },
                    axisTick: { // 
                        show: false
                    },
                    axisLine: {
                        lineStyle: { // lineStyle
                            color: [
                                [0.25, 'rgba(29, 152, 255, 1)'],//  
                                [0.75, 'rgba(9, 109, 217, 1)'],    
                                [1, 'rgba(114, 46, 209, 1)'],

                            ],
                            width: 10, //
                            shadowColor: '#fff', //
                            shadowBlur: 0  //
                        }
                    },
                    splitLine: {
                        length: 10, // length
                        lineStyle: { // lineStylelineStyle
                            width: 3,
                            color: 'transparent', //
                            shadowColor: '#fff', //
                            shadowBlur: 0
                        }
                    },
                }
            ]
        };

        this.chart.setOption(option);
    }

    public render() {
        return (
            <div ref={this.containerRef} style={{ height: "100%" }}>
                <div ref={this.chartRef} style={{ height: "100%" }} />
            </div>
        );
    }
}
